@font-face {
    font-family: 'Inconsolata';
    src: url('InconsolataNFP-Regular.woff2') format('woff2'),
        url('InconsolataNFP-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Inconsolata';
    src: url('InconsolataNFP-Bold.woff2') format('woff2'),
        url('InconsolataNFP-Bold.woff') format('woff');
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}